import { dir } from "dir";
import { createWriteStream, dir as dirSync } from "fs-jetpack";
import { apiContext } from "service-srv";
import { dirname } from "path";
export const _ = {
  url: "/_upload",
  async api(body: any) {
    const { req } = apiContext(this);
    let url = "";
    await req.multipart(async (e) => {
      const stream = e.file?.stream;
      const d = new Date();
      if (stream) {
        const path = `${d.getFullYear()}-${d.getMonth()}/${d.getDate()}/${d.getTime()}-${e.file?.name
          ?.replace(/\s+/g, "-")
          .toLowerCase()}`;
        const fpath = dir.path(`../content/upload/${path}`);
        dirSync(dirname(fpath));
        url = `/_file/${path}`;
        new Promise((resolve, reject) => {
          stream
            .on("error", (error) => {
              reject(error);
            })
            .pipe(createWriteStream(fpath))
            .on("error", (error) => reject(error))
            .on("finish", () => resolve(fpath));
        });
      }
    });

    return url;
  },
};
function toArrayBuffer(buffer: Buffer) {
  return buffer.buffer.slice(
    buffer.byteOffset,
    buffer.byteOffset + buffer.byteLength
  );
}
