import crypto from "crypto";
import { dir } from "dir";
import { existsAsync, readAsync, writeAsync } from "fs-jetpack";
import { apiContext } from "service-srv";
const cache = {} as Record<string, { content: string; etag: string }>;

export const _ = {
  url: "/_prasi/**",
  async api() {
    const { req, res } = apiContext(this);
    res.setHeader("Access-Control-Allow-Origin", "*");

    const sendCached = (name: string, content: any) => {
      if (!cache[name])
        cache[name] = {
          content,
          etag: crypto.createHash("md5").update(content).digest("hex"),
        };

      res.setHeader("etag", cache[name].etag);
      res.send(cache[name].content);
    };

    const action = {
      _: () => {
        res.json({ prasi: "v1" });
      },
      prisma: async () => {
        const path = req.params._.split("/").slice(1).join("/");
        const pdir = dir.path(`db/node_modules/.gen/${path}`);
        if (await existsAsync(pdir)) {
          res.type("text/x.typescript");
          const content = await readAsync(pdir, "utf8");
          sendCached("prisma", content);
        }
      },
      "api-types": async () => {
        const pdir = dir.path("srv/api.d.ts");
        if (await existsAsync(pdir)) {
          res.type("text/x.typescript");

          let result = await readAsync(pdir, "utf8");
          if (result) {
            if (!result.startsWith("/* DB-CLEANED */")) {
              const parr = result.split("\n");
              for (const [k, v] of Object.entries(parr)) {
                if (v.trim().startsWith("const db: WebGlobal")) {
                  parr[k] = "";
                }
              }
              parr.unshift("/* DB-CLEANED */");
              result = parr.join("\n");
              await writeAsync(pdir, result);
            }
          }

          sendCached("api-types", result);
        }
      },
      "api-entry": async () => {
        try {
          const apiEntry = await import("../../../../app/gen/srv/api/entry");
          res.type("application/json");
          sendCached("api-entry", JSON.stringify(apiEntry));
        } catch (e) {
          return "API entry not found";
        }
      },
    }[req.params._.split("/")[0]];

    if (action) await action();
  },
};
